import { Component, OnInit } from '@angular/core';
import { NavController, ActionSheetController, LoadingController, AlertController } from '@ionic/angular';
import { WitePrompt,  AppMin } from "../core"
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { Storage } from '@ionic/storage';//本地存储
import { File } from '@ionic-native/file/ngx';
import { Router, ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-mysetdata',
  templateUrl: './mysetdata.page.html',
  styleUrls: ['./mysetdata.page.scss'],
})
export class MysetdataPage implements OnInit {
  test_imgsrc;
  loader;
  cname = "未登录";
  sex = "";
  sexShow = "";
  myDate = "";
  qianming = "什么也没写╮(╯_╰)╭";
  userImg = "../assets/img/头像.png";
  header_class;

  cities: any[];
  constructor(public navCtrl: NavController, public ActivatedRoute: ActivatedRoute, public Router: Router,  public actionSheetCtrl: ActionSheetController, public loadingCtrl?: LoadingController, private camera?: Camera, public storage?: Storage, public appmin?: AppMin) {

    if (this.appmin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class_md";
      console.log("判断为安卓系统 设置状态栏样式")
    }
    console.log("constructor")

    this.addUserData();//加载用户数据
   




  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MysetdataPage');
    this.addStorageData();
  }

  async send(value, isshow?: boolean) {


    if (isshow) {
      this.loader = await this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000

      });
      await this.loader.present();
    }

    this.appmin.get(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


  }
  jianjie_click() {
    //跳转简介页面
    let obj = {
      jianjie: this.qianming
    }
    //  this.navCtrl.push(MysetdataJianjiePage, obj);
    this.Router.navigate(["mysetdata-info"],{queryParams:obj})

  }

  async shezsex() {
    //设置性别按钮
    WitePrompt("设置性别按钮被单击");
    let actionSheet = await this.actionSheetCtrl.create(
     {
     
      buttons: [
        {
          text: '男',
        
          icon: "ios-male",
          handler: () => {
            WitePrompt('男_click');
            this.sex = "0";
            this.sexShow = "男";
          }
        }, {
          text: '女',
          icon: "ios-female",
          handler: () => {
            WitePrompt('女_click');
            this.sex = "1";
            this.sexShow = "女";
          }
        }, {
          text: '取消',
          role: 'cancel',
          // icon: "ios-log-out",
          handler: () => {
            WitePrompt('取消_click');
          }
        }
      ]
    });
    actionSheet.present();


  }
  set_date() {
    //修改日期按钮
    WitePrompt("修改日期按钮click");
  }
  baoc_click() {
    //保存按钮
    this.setUserData();
  }
  setUserData() {
    //设置用户数据
    let obj = {
      type: "setUserData",
      userId: this.appmin.getUserid(),
      cname: this.cname,
      sex: this.sex,
      stratDate: this.myDate,
      qianming: this.qianming

    }
    this.send(JSON.stringify(obj), true);

  }
  addUserData() {
    //加载用户数据
    let obj = {
      type: 'addUserData_mySetData',
      userId: this.appmin.getUserid()
    }
    this.send(JSON.stringify(obj), false);
  }
  qianming_click(value) {
    //输入签名
    if (value.length >= 20) {
      value = value.substring(0, 20);
    }
    this.qianming = value;
  }
  cname_keyup(value) {
    //输入昵称inp
    if (value.length >= 8) {
      value = value.substring(0, 8);

    }
    this.cname = value;

  }
  async  selectuserimg_click() {
    //选择头像按钮

    //$("#file0").trigger("click");

    let actionSheet = await this.actionSheetCtrl.create({
      header: '上传头像',
      buttons: [
        {
          text: '图库上传',
          role: 'destructive',
       
          handler: () => {
            WitePrompt('上传头像click');
            this.userSelectImg();
          }
        }, {
          text: '拍照上传',
      
          handler: () => {
            WitePrompt('拍照上传_click');
            this.Photograph();
          }
        }, {
          text: '取消',
          role: 'cancel',
        
          handler: () => {
            WitePrompt('取消_click');
          }
        }
      ]
    });
    actionSheet.present();
  }

  Photograph() {
    //拍照上传

    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.FILE_URI,//返回值格式
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      targetWidth: 400,
      targetHeight: 400,
      allowEdit: true,  //选择前编辑图像
      cameraDirection: 0,//使用前置相机


    }
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      //let base64Image = 'data:image/jpeg;base64,' + imageData;
      console.log('imageData='+imageData)
      this.getBase64Image(imageData);

    }, (err) => {
      // Handle error
       console.log('相机出错='+err);
    });
  }
  userSelectImg() {
    //从图库上传
//console.log("userSelectImg1")
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.FILE_URI,//返回值格式
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      targetWidth: 400,
      targetHeight: 400,
      allowEdit: true,  //选择前编辑图像
      cameraDirection: 1,//使用前置相机
      sourceType: 2 //从图库选择

    }
 //   console.log("userSelectImg2")
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      //let base64Image = 'data:image/jpeg;base64,' + imageData;
      console.log('imageData='+imageData)
      this.getBase64Image(imageData);


      //alert(imageData);
    }, (err) => {
      // Handle error
      console.log('相机出错='+err);
    });

  }

  getBase64Image(imgUri) {

    //imgUri=imgUri.replace('file://','');  //解决WKWebView兼容问题
    //console.log('getBase64Image_uri=' + imgUri);
    // imgUri=normalizeURL(imgUri); //解决WKWebView兼容问题
    imgUri=(<any>window).Ionic.WebView.convertFileSrc(imgUri);


    console.log("getBase64Image!="+imgUri)
    let this1 = this;
    let img = new Image();
    img.src = imgUri;
    img.onload = function () {
      console.log("getBase64Image img.onload!")
      var canvas = document.createElement("canvas");
      canvas.width = img.width;
      canvas.height = img.height;

      var ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0, img.width, img.height);
      //var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
      var dataURL = canvas.toDataURL("image/jpeg");

      //return dataURL;
      this1.userImg = dataURL;
      let upobj = {
        type: "updataUserImg",
        userid: this1.appmin.getUserid(),
        userImgUri: dataURL
      }

      this1.appmin.post((upobj)).subscribe((data) => {
        this1.dataHandle(data);
      })
   
   
       console.log("getBase64Image_json=" + JSON.stringify(upobj))

    }

  }
  dataHandle(meg: any) {
    if (meg['type'] == "setUserData_ok") {
      //更新数据完成 准备跳转
      this.navCtrl.pop();
    }
    if (meg['type'] == "addUserData_mySetData_ret") {
      //收到加载用户数据
      // this.cname = meg["CName"];
      this.cname = meg["CName"];
      this.storage.set("CName", meg["CName"]);

      if (meg["sex"] == "0") {
        this.sexShow = "男";
        this.storage.set("userSex", "男");
      } else {
        this.sexShow = "女";
        this.storage.set("userSex", "女");

      }

      let img: string = meg["UserImgUrl"];
      console.log('userimg=' + img);
      if (img.trim() != "") {
        this.userImg = meg["UserImgUrl"];
        this.storage.set("userimg", meg['UserImgUrl']);
        this.appmin.setUserImg(meg["UserImgUrl"]);
      }
      else {
        this.userImg = "../assets/img/头像.png";
        this.storage.set("userimg", "../assets/img/头像.png");
      }


      this.myDate = meg["startdate"];
      this.storage.set("startdate", meg['startdate']);
      this.qianming = meg["qianming"];
      this.storage.set("gexingqianming", meg['qianming']);

    }
    if (meg['type'] == "updataUserImg_ret") {
      //alert(JSON.stringify(meg));

    }
  }
  addStorageData() {
    WitePrompt("从本地存储加载用户数据");
    let this1 = this;
    this.storage.ready().then(function () {

      this1.storage.get('userSex').then((val) => {
        this1.sexShow = val;


      });



      this1.storage.get('CName').then((val) => {
        this1.cname = val;
      });



      this1.storage.get('gexingqianming').then((val) => {
        this1.qianming = val;
        // WitePrompt("gexingqianming"+val);
      });

      this1.storage.get('userimg').then((val) => {
        this1.userImg = val;
      });


      this1.storage.get('startdate').then((val) => {
        this1.myDate = val;
        // WitePrompt("gexingqianming"+val);
      });
    })




    // this.navCtrl.resize();
  }

  ngOnInit() {
  }

}
